@tailwind base;
@tailwind components;
@tailwind utilities;

/* === Réglage hauteur panneau "Avis" === */
:root{
  --reviews-offset-mobile: 2rem;   /* distance au-dessus des avis sur mobile */
  --reviews-offset-desktop: -8rem;/* distance au-dessus des avis sur desktop */
  --reviews-offset: var(--reviews-offset-mobile);
}

@media (min-width: 768px){
  :root{ --reviews-offset: var(--reviews-offset-desktop); }
}

/* Marquee : défilement horizontal continu et lent */
:root { --marquee-duration: 65s; } /* valeur par défaut */
.marquee {
  width: max-content;
  animation: marquee var(--marquee-duration) linear infinite;
  will-change: transform;
}

/* Respecte l'accessibilité : pas d'animation si l'utilisateur la réduit */
@media (prefers-reduced-motion: reduce) {
  .marquee { animation: none; }
}

@keyframes marquee {
  from { transform: translateX(0); }
  to   { transform: translateX(-50%); } /* 2 groupes => on translate la moitié */
}

/* Fondu aux bords pour un rendu propre */
.mask-fade {
  -webkit-mask-image: linear-gradient(90deg, transparent, #000 6%, #000 94%, transparent);
  mask-image: linear-gradient(90deg, transparent, #000 6%, #000 94%, transparent);
}
